.window {
  display: flex;
  background-color: #4272b6;
  border-radius: 6px;
  padding: 50px;
  margin: 40px;
}
.game-window {
  min-width: 1000px;
  width: 100%;
  min-height: 600px;
  height: 50%;
}

.lobby-header {
  border: 1px solid black;
  border-radius: 3px;
  background-color: white;
  margin-left: 360px;
  margin-bottom: 20px;
  width: 600px;
  height: 100px;
  word-wrap: break-word;
}
.lobby-header h1 {
  margin-top: 0px;
  font-size: 50px;
}

.lobby-header p {
  margin-top: -30px;
  margin-bottom: 30px;
}

.lobby-button {
  font-family: "Gadugi";
  background-color: rgb(255, 255, 255);

  border-radius: 6px;
  opacity: 1;
  width: 116px;
  height: 40px;
  margin: 9px 10px;
  transform: translateX(-400px);
}

.lobby-button:hover {
  background-color: rgb(232, 234, 245);
}

.rules-container {
  overflow-y: scroll;
  padding: 0px 20px;
  text-align: left;
  background-color: white;
  margin-top: -240px;
  width: 560px;
  height: 463px;
  border: 1px solid black;
  border-radius: 6px;
  margin-left: 360px;
}

.rules-container h2 {
  font-size: 20px;
  margin-bottom: -10px;
}
.rules-container p {
  font-size: 14px;
}

.player-display {
  margin-top: -120px;
  width: 250px;
  height: 300px;
  border: 1px solid black;
  border-radius: 8px;
  background-color: white;
}

.player-display .player-display-header {
  margin-top: -16px;
  margin-bottom: 16px;
  height: 45px;
  border-radius: 6px;
  background: #263238;
  position: relative;
  cursor: pointer;
}
.player-display .player-display-header p {
  display: block;
  padding: 0 1em 0 2em;
  color: #fff;
  font-weight: 700;
  line-height: 45px;
}

.player-display-list p {
  margin: 0px;
}

.chat-window {
  right: 40px;
  width: 300px;
}

.chat-window p {
  margin: 0;
}
.chat-window .chat-header {
  height: 45px;
  border-radius: 6px;
  background: #263238;
  position: relative;
  cursor: pointer;
}
.chat-window .chat-header p {
  display: block;
  padding: 0 1em 0 2em;
  color: #fff;
  font-weight: 700;
  line-height: 45px;
}

.chat-window .chat-body {
  height: 500px;
  border: 1px solid #263238;
  background: #fff;

  position: relative;
}
.chat-window .chat-body .message-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.chat-window .chat-body .message-container::-webkit-scrollbar {
  display: none;
}
.chat-window .chat-body .message {
  height: auto;
  padding: 10px;
  display: flex;
}

.chat-window .chat-body .message .message-content {
  width: auto;
  height: auto;
  display: flex;
  align-items: center;
  overflow-wrap: break-word;
  word-break: break-word;
  text-align: left;
}

.chat-window .chat-body .message .message-meta {
  font-weight: bold;
}

.chat-window .chat-footer {
  height: 40px;
  border: 1px solid #263238;
  border-top: none;
  display: flex;
}

.chat-window .chat-footer input {
  height: 100%;
  flex: 85%;
  border: 0;
  padding: 0 0.7em;
  font-size: 1em;
  border-right: 1px dotted #607d8b;

  outline: none;
  font-family: "Open Sans", sans-serif;
}

.chat-window .chat-footer button {
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15%;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 25px;
  color: lightgray;
}

.chat-window .chat-footer button:hover {
  color: #43a047;
}
.hide {
  opacity: 0 !important;
}

.admin-message {
  font-weight: bold;
}
